import React, { useState } from 'react';
import { MioEditor } from '../lib';

const App = () => {
  // It gets content save at browser local storage or default initial value defined below
  const [value, setValue] = useState(JSON.parse(localStorage.getItem('content')) || initialValue);

  // This function is called when user edit content at mio editor with new json value
  const onValueChange = value => {
    setValue(value);
    saveInLocalStorage(value);
  };

  const saveInLocalStorage = value => {
    const content = JSON.stringify(value);
    localStorage.setItem('content', content);
    console.log(content);
  }

  return (
    <MioEditor
      value={value}
      onValueChange={onValueChange}
    />
  );
};

